import G6 from '@antv/g6';

G6.registerNode(
    'card-node',
    {
        /**
         * 绘制节点，包含文本
         * @param  {Object} cfg 节点的配置项
         * @param  {G.Group} group 图形分组，节点中图形对象的容器
         * @return {G.Shape} 返回一个绘制的图形作为 keyShape，通过 node.get('keyShape') 可以获取。
         * 关于 keyShape 可参考文档 核心概念-节点/边/Combo-图形 Shape 与 keyShape
         */
        draw(cfg, group) {
            const r = 5;
            const borderColor = "#5f95ff";
            const BackgroundColor = "#eff4ff";
            const w = cfg.size[0];
            const h = cfg.size[1];
            const labelW = cfg.size2[0];
            const hSub = cfg.size2[1];
            const info = cfg.info;
            const life = cfg.lifeList;
            const curXmbh = cfg.curXmbh;
            const oriX = 15; // 节点左上角坐标X
            const oriY = 10; // 节点左上角坐标Y
            const padding = 5;
            const infoMaxLine = 5;

            // 关键图形
            const keyShape = group.addShape('rect', {
                attrs: {
                    x: oriX,
                    y: oriY,
                    width: w,
                    height: h,
                    stroke: borderColor,
                    lineWidth: 2,       // 描边宽度
                    radius: r,
                    fill: "#fff",
                },
                name: "main-box",
                draggable: true, // 该图形是否允许被拖拽
            });

            // info-box
            group.addShape('rect', {
                attrs: {
                    x: oriX,
                    y: oriY,
                    width: w,
                    height: h - hSub,
                    stroke: borderColor,
                    lineWidth: 2,       // 描边宽度
                    fill: BackgroundColor,
                    radius: [r, r, 0, 0],
                },
                name: 'info-box',
                draggable: true,
            });

            // text 节点的共同属性
            const textAttrs = {
                textBaseline: 'top',
                fontSize: 16,
                lineHeight: 22,
                fill: '#000',
            };

            // 依次绘制 label 和 value
            const labelName = ['项目编号:', '项目名称:', '项目类型:'];
            const labelY = [
                oriY,
                oriY + (h - hSub) / infoMaxLine,
                oriY + (h - hSub) * 3 / infoMaxLine,
            ];
            const labelMaxLine = [1, 2, 2];
            for (let i = 0; i < 3; i++) {
                const key = Object.keys(info)[i];
                // label
                group.addShape('text', {
                    attrs: Object.assign({
                        text: labelName[i],
                        x: oriX + padding,
                        y: labelY[i] + padding,
                        fontWeight: 700,
                    }, textAttrs),
                    name: key + '-label',
                });

                // value
                const valueAttrs = Object.assign({
                    text: superLongTextHandle(info[key], w - labelW - 5, 16, labelMaxLine[i]),
                    x: oriX + labelW,
                    y: labelY[i] + padding,
                }, textAttrs);
                if (String(key) === 'xmbh' && info[key] === curXmbh) { // 流程图里的当前项目高亮
                    valueAttrs.fill = '#ff0000';
                }
                group.addShape('text', {
                    attrs: valueAttrs,
                    name: key + '-value',
                });
            }

            // info-box 下面的 text
            group.addShape('text', {
                attrs: {
                    text: life[0],
                    textBaseline: 'top',
                    textAlign: "center",
                    x: oriX + w / 4,
                    y: oriY + h - hSub + padding,
                    fontSize: 16,
                    lineHeight: 22,
                    fill: 'rgba(0,0,0, 1)',
                },
                name: `life1`,
            });
            group.addShape('text', {
                attrs: {
                    text: life[1],
                    textBaseline: 'top',
                    textAlign: "center",
                    x: oriX + w * 3 / 4,
                    y: oriY + h - hSub + padding,
                    fontSize: 16,
                    lineHeight: 22,
                    fill: 'rgba(0,0,0, 1)',
                },
                name: `life2`,
            });

            // 添加一条竖线隔开两个life
            group.addShape('path', {
                attrs: {
                    path: [
                        ['M', oriX + w / 2, oriY + h - hSub],
                        ['L', oriX + w / 2, oriY + h]
                    ],
                    stroke: borderColor,
                    lineWidth: 2,       // 描边宽度
                },
                name: 'line',
            });

            // 如果有子节点，则绘制展开按钮
            /*cfg.children &&
            group.addShape('marker', {
                attrs: {
                    x: oriX + w,
                    y: oriY + h / 2,
                    r: 10,
                    cursor: 'pointer',
                    symbol: cfg.collapsed ? G6.Marker.expand : G6.Marker.collapse,
                    stroke: '#666',
                    lineWidth: 1,
                    fill: '#fff',
                },
                name: 'collapse-icon',
            });*/

            return keyShape;
        },
        /**
         * 绘制后的附加操作，默认没有任何操作
         * @param  {Object} cfg 节点的配置项
         * @param  {G.Group} group 图形分组，节点中图形对象的容器
         */
        // afterDraw(cfg, group) {
        // },
        /**
         * 更新节点，包含文本
         * @override
         * @param  {Object} cfg 节点的配置项
         * @param  {Node} node 节点
         */
        // update(cfg, node) {
        // },
        /**
         * 更新节点后的操作，一般同 afterDraw 配合使用
         * @override
         * @param  {Object} cfg 节点的配置项
         * @param  {Node} node 节点
         */
        // afterUpdate(cfg, node) {
        // },
        /**
         * 设置节点的状态，主要是交互状态，业务状态请在 draw 方法中实现
         * 在需要使用动画来响应状态变化时需要被复写
         * @param  {String} name 状态名称
         * @param  {Object} value 状态值
         * @param  {Node} node 节点
         */
        setState(name, value, node) {
            if (name === 'collapsed') {
                const marker = node.get('group').find((ele) => ele.get('name') === 'collapse-icon');
                const icon = value ? G6.Marker.expand : G6.Marker.collapse;
                marker.attr('symbol', icon);
            }
        },
        /**
         * 获取锚点（相关边的连入点）
         * @param  {Object} cfg 节点的配置项
         * @return {Array|null} 锚点（相关边的连入点）的数组,如果为 null，则没有控制点
         */
        // getAnchorPoints(cfg) {
        // },
    },
    // 继承内置节点类型的名字，例如基类 'single-node'，或 'circle', 'rect' 等
    // 当不指定该参数则代表不继承任何内置节点类型
    // extendedNodeType,
);

// G6换行符处理超长文本
function superLongTextHandle(str, maxWidth, fontSize, maxLine = 3, curLine = 1) {
    const ellipsis = '...';
    const ellipsisLength = G6.Util.getLetterWidth(ellipsis, fontSize);
    let currentWidth = 0;
    let res = str;
    // 区分汉字和字母
    const pattern = new RegExp("[\u4E00-\u9FA5]+");
    str.split("").forEach((letter, i) => {
        if (curLine >= maxLine) {
            if (currentWidth > maxWidth - ellipsisLength) return;
        } else {
            if (currentWidth > maxWidth) return;
        }
        if (pattern.test(letter)) {
            // 中文字符
            currentWidth += fontSize;
        } else {
            // 根据字体大小获取单个字母的宽度
            currentWidth += G6.Util.getLetterWidth(letter, fontSize);
        }
        // 第3行超出的文本显示...
        if (curLine >= maxLine) {
            if (currentWidth > maxWidth - ellipsisLength) {
                res = `${str.substr(0, i)}${ellipsis}`;
            }
        } else {
            if (currentWidth > maxWidth) {
                curLine += 1;
                res = `${str.substr(0, i)}\n${superLongTextHandle(
                    str.substr(i), maxWidth, fontSize, maxLine, curLine)}`;
            }
        }
    });
    return res;
}
