<template>
  <div id="app">
    <el-dialog title="流程图" :visible.sync="flowDialog" width="90%" top="5%">
      <div id="flow-title">
        <div><strong>项目编号: </strong><span style="color: #ff0000">{{ curXmxx.xmbh }}</span></div>
        <div><strong>项目名称: </strong>{{ curXmxx.xmmc }}</div>
        <div><strong>项目类型: </strong>{{ curXmxx.xmlx }}</div>
      </div>
      <div id="flow-chart"></div>
    </el-dialog>

    <el-container>
      <el-header>
        <!-- 筛选条件表单 -->
        <el-form class="searchForm" :inline="true" :model="searchbox">
          <!-- 默认展示的 -->
          <el-form-item label="开关按钮">
            <el-switch v-model="searchbox.nolike"></el-switch>
          </el-form-item>
          <!-- 配置添加的 -->
          <el-form-item v-for="(item, index) in checkedList" :key="index" :label="item.name">
            <template v-if="item.type === 'input'">
              <el-input v-model="item.modelValue" :placeholder="item.placeholder" :style="item.style"
                        clearable></el-input>
            </template>
            <template v-if="item.type === 'input2'">
              <el-input v-model="item.modelValue1" :placeholder="item.placeholder1" :style="item.style"
                        clearable oninput="value=value.replace(/[^0-9.]/g,'')">
                <template slot="append">{{ item.suffix }}</template>
              </el-input>
              至
              <el-input v-model="item.modelValue2" :placeholder="item.placeholder2" :style="item.style"
                        clearable oninput="value=value.replace(/[^0-9.]/g,'')">
                <template slot="append">{{ item.suffix }}</template>
              </el-input>
            </template>
            <template v-if="item.type.includes('select')">
              <el-select v-model="item.modelValue" :multiple="item.type === 'selects'"
                         :placeholder="item.placeholder" collapse-tags :style="item.style">
                <el-option v-for="(opt, idx) in item.optionList" :key="idx" :label="opt.label"
                           :value="opt.value" :disabled="opt.disabled"></el-option>
              </el-select>
            </template>
            <template v-if="item.type === 'datePicker'">
              <el-date-picker v-model="item.modelValue1" :placeholder="item.placeholder1"
                              :style="item.style" value-format="yyyy-MM-dd"
                              clearable></el-date-picker>
              至
              <el-date-picker v-model="item.modelValue2" :placeholder="item.placeholder2"
                              :style="item.style" value-format="yyyy-MM-dd"
                              clearable></el-date-picker>
            </template>
          </el-form-item>
          <!-- 筛选 -->
          <el-form-item>
            <el-popover placement="bottom-start" trigger="hover">
              <div class="column">
                <div class="row">
                  <el-button type="text" @click="operateBtn('all')">全选</el-button>
                  <div style="width: 1px; height: 14px; background: #409eff; margin: 0 10px;"></div>
                  <el-button type="text" @click="operateBtn('empty')">清空</el-button>
                  <!-- <div style="width: 1px; height: 14px; background: #409eff; margin: 0 10px;"></div>
                  <el-button type="text" @click="operateBtn('default')">默认</el-button> -->
                </div>
                <el-checkbox-group v-model="checkedList">
                  <div class="row checkboxli" v-for="(item, index) in allList" :key="index">
                    <el-tooltip :disabled="item.tipsDisabled" effect="light"
                                placement="right">
                      <template slot="content">
                        <div v-for="(text, index) in item.tipsText" :key="index">
                          {{ text }}
                        </div>
                      </template>
                      <el-checkbox :label="item" :disabled="item.disabled">
                        {{ item.name }}
                      </el-checkbox>
                    </el-tooltip>
                  </div>
                </el-checkbox-group>
              </div>
              <el-button slot="reference" type="primary">+ 筛选条件</el-button>
            </el-popover>
          </el-form-item>
        </el-form>
        <!-- 操作按钮 -->
        <el-form :inline="true" style="text-align: left; position: relative;">
          <el-form-item>
            <el-button type="primary" @click="searchClick">查询</el-button>
            <el-button type="primary" @click="viewFlowChart">查看流程图</el-button>
          </el-form-item>
        </el-form>
      </el-header>
      <el-main>
        查询参数：{{queryPara}}
      </el-main>
    </el-container>
  </div>
</template>

<script>
import G6 from '@antv/g6';

require('../card-node')

export default {
  data() {
    return {
      searchbox: {
        nolike: false,
      },
      params: {},  // 查询参数
      xmlxList: [{
        value: '',
        label: '全部'
      }, {
        value: '1',
        label: '项目类型1'
      }, {
        value: '2',
        label: '项目类型2'
      }, {
        value: '3',
        label: '项目类型3'
      }, {
        value: '4',
        label: '项目类型4'
      }, {
        value: '5',
        label: '项目类型5'
      }],
      dxList: [{
        value: '',
        label: '全部'
      }, {
        value: '1',
        label: '选项1'
      }, {
        value: '2',
        label: '选项2'
      }, {
        value: '3',
        label: '选项3'
      }],
      allList: [ // 全部的筛选条件表单配置
        {
          name: '输入框', // 标签名
          value: 'xmbh', // 变量名
          // 类型: 下拉框 单选select 多选selects, 输入框input, 日期选择器datePicker, 范围input2
          type: 'input',
          modelValue: '', // 绑定到控件上的值: 下拉框 单选'' 多选[], 输入框'', 日期选择器'', 范围''
          placeholder: '请输入项目编号',
          // suffix: '', // 后缀
          style: 'width: 150px', // 控件样式
          default: false, // 是否默认显示
          disabled: false, // 是否禁用勾选
          optionList: [], // 当type为下拉框，该属性才有用
          /*optionList: [{
              value: '-1',
              label: '全部',
              disabled: false // 是否禁用选择
          }]*/
          tipsDisabled: true, // 是否禁用文字悬浮提示
          tipsText: [] // 悬浮提示显示的文字（数组中每个元素独占一行）
        },
        {
          name: '单选框-项目类型',
          value: 'xmlx',
          type: 'select',
          modelValue: '',
          placeholder: '请选择项目类型',
          style: 'width: 400px',
          default: false,
          disabled: false,
          optionList: [],
          tipsDisabled: true,
          tipsText: []
        },
        {
          name: '范围框-日期',
          value1: 'fzrqStart', // 变量: 开始日期
          value2: 'fzrqEnd', // 变量: 结束日期
          type: 'datePicker',
          modelValue1: '',
          modelValue2: '',
          placeholder1: '开始日期',
          placeholder2: '结束日期',
          style: 'width: 150px',
          default: false,
          disabled: false,
          optionList: [],
          tipsDisabled: true,
          tipsText: []
        },
        {
          name: '范围框-面积',
          value1: 'mjStart', // 变量: 开始建筑面积
          value2: 'mjEnd', // 变量: 结束建筑面积
          type: 'input2',
          modelValue1: '',
          modelValue2: '',
          placeholder1: '面积',
          placeholder2: '面积',
          suffix: '平方米', // 后缀
          style: 'width: 200px',
          default: false,
          disabled: true,
          optionList: [],
          tipsDisabled: false,
          tipsText: ['【项目类型】选择以下的，此选项开放：', '项目类型1', '项目类型2']
        },
        {
          name: '单选框',
          value: 'dx',
          type: 'select',
          modelValue: '',
          placeholder: '请选择',
          style: 'width: 150px',
          default: false,
          disabled: true,
          optionList: [],
          tipsDisabled: false,
          tipsText: ['【项目类型】选择以下的，此选项开放：', '项目类型3']
        },
      ],
      defaultList: [], // 默认的筛选条件
      requiredList: [], // 必选的筛选条件 (至少需要选择的)
      checkedList: [], // 已选择的筛选条件
      isOpenMj: false, // 是否放开筛选条件: 面积
      isOpenDx: false, // 是否放开筛选条件：单选框
      flowDialog: false,  // 是否显示流程图弹出框
      curXmxx: {}, // 流程图上的当前项目信息
      queryPara: {}, // 请求参数
    }
  },
  created() {
    // 初始化筛选表单配置
    this.allList.forEach(item => {
      if (item.default) { // 默认的
        this.defaultList.push(item)
        this.checkedList.push(item)
      }
      // 对 optionList 赋值
      if (item.value === 'xmlx') {
        item.optionList = this.xmlxList
      } else if (item.value === 'dx') {
        item.optionList = this.dxList
      }
    })

    this.fetchData();
  },
  watch: {
    checkedList: {
      deep: true,
      handler(newList) {
        // 去掉项目类型的筛选框时，newList不包含xmlx，默认禁用 范围框-面积、单选框
        this.isOpenMj = false;
        this.isOpenDx = false;
        newList.forEach(item => {
          if (item.value === 'xmlx') {
            // 判断是否放开筛选条件: 范围框-面积。
            if (['1', '2'].includes(item.modelValue)) {
              this.isOpenMj = true;
            } else {
              this.isOpenMj = false;
            }
            // 判断是否放开筛选条件: 单选框。
            if (['3'].includes(item.modelValue)) {
              this.isOpenDx = true;
            } else {
              this.isOpenDx = false;
            }
          }
        })
      }
    },
    isOpenMj(newVal) {
      this.allList.forEach(item => {
        if (['范围框-面积'].includes(item.name)) {
          item.disabled = !newVal;
          item.tipsDisabled = newVal;
        }
      })
      // 如果是关闭，那就去掉筛选框
      if (!newVal) {
        this.checkedList = this.checkedList.filter(item => {
          // 保留 除 范围框-面积 外的其他筛选条件
          return !['范围框-面积'].includes(item.name);
        })
      }
    },
    isOpenDx(newVal) {
      this.allList.forEach(item => {
        if (['单选框'].includes(item.name)) {
          item.disabled = !newVal;
          item.tipsDisabled = newVal;
        }
      })
      // 如果是关闭，那就去掉筛选框
      if (!newVal) {
        this.checkedList = this.checkedList.filter(item => {
          // 保留 除 单选框 外的其他筛选条件
          return !['单选框'].includes(item.name);
        })
      }
    }
  },
  methods: {
    // 查询
    searchClick() {
      // 清空 params
      this.params = {};
      // 将筛选条件封装到 params
      this.checkedList.forEach(item => {
        if (item.value1 != null && item.value1 !== '') {
          this.params[item.value1] = item.modelValue1;
          this.params[item.value2] = item.modelValue2;
        } else {
          this.params[item.value] = item.modelValue;
        }
      })

      this.fetchData();
    },
    fetchData() { // 抓取后台数据
      this.queryPara = Object.assign({}, this.searchbox, this.params);
      // 请求数据
      /*axios.post('api/xxx/list', para).then(function (res) {
          console.log(res);
      }).catch(function (error) {
          console.log(error);
      })*/
    },
    // 筛选操作
    operateBtn(type) {
      if (type === 'all') { // 全选（没有被禁用的筛选条件）
        this.checkedList = this.allList.filter(item => {
          return !item.disabled;
        })
      } else if (type === 'empty') { // 清空
        // this.checkedList = [...this.requiredList]
        this.checkedList = []
      } else { // 默认
        this.checkedList = this.defaultList
      }
    },
    // 重置筛选条件表单
    /*reset() {
        this.checkedList = [...this.requiredList]
    },*/
    viewFlowChart() {
      // api返回的结果
      const res = {
        data: {
          data: {
            curXmxx: {
              xmlx: "项目类型1",
              xmmc: "项目名称1[续办]",
              xmbh: "111111"
            },
            lifecycleFlow: {
              lifeList: [
                "[新建]\n2021-11-09 10:33:43",
                "[办结归档]\n2021-11-23 10:15:33"
              ],
              children: [
                {
                  lifeList: [
                    "[续办]\n2024-10-14 10:58:39",
                    "[办结归档]\n2024-10-14 15:27:14"
                  ],
                  id: "222222",
                  info: {
                    xmbh: "222222",
                    xmmc: "项目名称222222[续办]",
                    xmlx: "项目类型1"
                  }
                }
              ],
              id: "111111",
              info: {
                xmbh: "111111",
                xmmc: "项目名称111111",
                xmlx: "项目类型2"
              }
            }
          }
        }
      }
      // 处理
      const resData = res.data.data;
      this.curXmxx = resData.curXmxx;
      const flowData = resData.lifecycleFlow;
      const curXmbh = this.curXmxx.xmbh; // 当前项目编号

      this.flowDialog = true;
      // 渲染画布，确保画布的div已存在
      const judgeFlowDiv = setInterval(() => {
        const container = document.getElementById('flow-chart');
        let graph = null;
        if (container !== null) {
          container.innerText = ''; // 清空画布
          window.clearInterval(judgeFlowDiv); // 清除定时器

          const nodeWidth = 360;  // 关键图形节点宽
          const nodeHeight = 155; // 关键图形节点高
          const labelWidth = 80;  // 标签宽，比如 项目编号
          const subHeight = 45;   // 生命周期节点高
          const indent = nodeWidth + 100; // 树图的列间间距

          // 初始化画布
          graph = new G6.TreeGraph({
            container: container,
            width: container.scrollWidth, // 默认为画布容器宽度
            height: container.scrollHeight, // 默认为画布容器高度
            modes: {
              default: [
                // 拖拽画布
                {
                  type: 'drag-canvas',
                  allowDragOnItem: true, // 允许用户在节点/边/combo上拖拽时响应
                },
                'zoom-canvas',  // 缩放画布
              ],
            },
            // 全局节点配置
            defaultNode: {
              type: 'card-node',
              size: [nodeWidth, nodeHeight],
              size2: [labelWidth, subHeight],
              curXmbh: curXmbh,
              // 该节点可选的连接点集合
              anchorPoints: [
                // [0.5, 0],   // 上
                [1, 0.5],   // 右
                // [0.5, 1],   // 下
                [0, 0.5],   // 左
              ],
            },
            // 全局边配置
            defaultEdge: {
              type: 'cubic-horizontal',   // 边类型
              style: {
                endArrow: true,         // 边末端是否显示箭头
                stroke: "skyblue",      // 边颜色
                lineWidth: 2,           // 边宽度
              },
            },
            layout: {
              type: 'indented',
              direction: 'LR', // 树布局的方向，默认为 'LR'
              indent: indent, // 列间间距
              dropCap: false, // 每个节点的第一个自节点是否位于下一行。默认为 true
              getHeight: () => {
                return -36 + nodeHeight + 50;
              },
            },
            // fitView: true,  // 将图适配到画布中
            // fitViewPadding: [20, 40, 50, 20],  // 画布上四周的留白宽度
          });

          // 节点的展开与关闭
          /*graph.on('node:click', (e) => {
              if (e.target.get('name') === 'collapse-icon') {
                  e.item.getModel().collapsed = !e.item.getModel().collapsed;
                  graph.setItemState(e.item, 'collapsed', e.item.getModel().collapsed);
                  graph.layout();
              }
          });*/

        }
        if (graph != null) {
          graph.data(flowData);
          graph.render();
          // 将图适配到画布中，仅在内容超出画布时进行适配
          graph.fitView(0, {onlyOutOfViewPort: true});
        }

        // 使图始终适配容器大小，防止创建的画布大小不对
        // if (typeof window !== 'undefined')
        //     window.onresize = () => {
        //         if (!graph || graph.get('destroyed')) return;
        //         if (!container || !container.scrollWidth || !container.scrollHeight) return;
        //         graph.changeSize(container.scrollWidth, container.scrollHeight);
        //     };
      }, 100);
    },
  },
}
</script>


<style scoped>
.el-main-div {
  border: 1px solid #DCDFE6;
  border-radius: 4px;
  text-align: left;
  padding-bottom: 20px;
  margin-right: 220px;
}

.el-header {
  height: auto !important;
  margin-top: 20px;
}

.el-main {
  text-align: center;
}

.el-form {
  text-align: left;
}

.searchForm .el-form-item {
  margin-right: 30px;
}

.el-container {
  text-align: center;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.column {
  display: flex;
  flex-direction: column;
}

.checkboxli {
  margin: 10px 0;
}

::v-deep .checkboxli .el-checkbox {
  margin-right: 0;
}

::v-deep .checkboxli .el-checkbox__label {
  padding-left: 15px;
}

.el-dialog__header {
  font-size: 18px;
  font-weight: 700;
}

.el-dialog__body {
  padding: 5px 0 0 0;
}

.el-dialog__body #flow-title {
  margin-left: 20px;
  margin-right: 20px;
  color: #121213;
  font-size: 16px;
}

.el-dialog__body #flow-chart {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  border: 1px solid #ccc;
  height: 450px;
}
</style>